"use client";
import AddStudent from "@/app/students/edits/AddStudent";
import DeleteStudents from "@/app/students/edits/DeleteStudents";
import EditStudents from "@/app/students/edits/EditStudents";
import useGlobalStore from "@/store/my-store";
import useAuthStore from "@/store/MyAuthState";
import { Button, Switch, Table } from "antd";
import axios from "axios";
import { useEffect, useState } from "react";

type UserType = {
  name: string;
  email: string;
  password: string;
  image: string;
  role: string;
};

function SrudentsPage() {
  const MyAuthState = useAuthStore();
  const [user, setUsers] = useState<UserType[] | []>([]);
  const [AddOpen, setAddOpen] = useState(false);
  const [editOpen, setEditOpen] = useState(false);
  const [EditStudent, setEditStudent] = useState({});
  const [deleteStudent, setDeleteStudent] = useState({});

  const showDrawer = () => {
    setAddOpen(true);
  };
  const showDrawerEdit = () => {
    setEditOpen(true);
  };

  const onClose = () => {
    setAddOpen(false);
  };
  const onCloseEdit = () => {
    setEditOpen(false);
  };

  useEffect(() => {
    axios
      .get(`https://nt.softly.uz/api/app/users`, {
        headers: { Authorization: `Bearer ${MyAuthState.token}` },
      })
      .then((res) => {
        setUsers(res.data);
      })
      .catch((e) => {
        console.log(e);
      })
      .finally(() => {
        console.log("getFinish");
      });
  }, []);
  console.log(user);

  return (
    <div className="flex flex-col items-center">
      <div className="flex gap-2 items-center justify-center  ">
        <Button>Umumiy son: {user.length}</Button>
      </div>

      <div className="flex flex-col my-5">
        {/* <AddStudent onClose={onClose} open={AddOpen} showDrawer={showDrawer} /> */}
        {/* <EditStudents
          editOpen={editOpen}
          showDrawerEdit={showDrawerEdit}
          onCloseEdit={onCloseEdit}
          EditStudent={EditStudent}
          setEditStudent={setEditStudent}
        /> */}
        <Table
          columns={[
            {
              title: "ID",
              dataIndex: "id",
              render: (id: any, student, s) => {
                return (
                  <span
                    className=" cursor-pointer "
                    onClick={() => {
                      setEditOpen(true);
                      setEditStudent(student);
                    }}
                  >
                    {id}
                  </span>
                );
              },
            },
            {
              title: "Ism",
              dataIndex: "firstName",
            },
            {
              title: "Familiya",
              dataIndex: "lastName",
            },
            {
              title: "Guruh",
              dataIndex: "group_id",
              render: (group_id) => {
                const group = state.groups.find((f) => {
                  return f.id === group_id;
                });
                return group?.name;
              },
            },
            {
              title: "Jinsi",
              dataIndex: "gender",
              render: (v, item: any) => {
                return (
                  <span key={item.id}>{v === "male" ? "Erkak" : "Ayol"}</span>
                );
              },
            },
            {
              title: "Active",
              dataIndex: "active",
              render: (v, studentt: any) => {
                return (
                  <div className="flex gap-1 items-center">
                    <Switch
                      checked={v}
                      onChange={(checked) => {
                        const localStudent: StudentType[] = JSON.parse(
                          localStorage.getItem("students") || "[]"
                        );

                        const new_students = localStudent.map((item) => {
                          if (item.id === studentt.id) {
                            return {
                              ...item,
                              key: item.id,
                              active: checked,
                            };
                          }
                          return item;
                        });

                        localStorage.setItem(
                          "students",
                          JSON.stringify(new_students)
                        );
                        useGlobalStore.setState({ students: new_students });
                      }}
                    />
                    <DeleteStudents
                      studentt={studentt}
                      setDeleteStudent={setDeleteStudent}
                      deleteStudent={deleteStudent}
                    />
                  </div>
                );
              },
            },
          ]}
          dataSource={state.students.map((i: any) => {
            return {
              ...i,
              key: i.id,
            };
          })}
        />
        <Button
          color="danger"
          type="text"
          variant="text"
          onClick={() => {
            if (window.confirm("Hammasini o'chirishni tasdiqlaysizmi?")) {
              localStorage.clear();
              alert("Hammasi o'chirildi!");
            }
            //ha gptdan oldim windowni
          }}
        >
          Hammasini ochirish{" "}
        </Button>
      </div>
    </div>
  );
}

export default SrudentsPage;
